/** @jsx jsx */
import { jsx, css, Global } from '@emotion/core'
import React from 'react';
import { createBrowserHistory } from 'history';
import OverlayScrollbars from 'overlayscrollbars';
import Homepage from '../page/homepage';
import Footer from './footer';
import Header from './header/header';
import HoverPage from './hover-page';
import ArticlePage from '../page/article';
import Block from './block';

const history = createBrowserHistory();
let initialPage: string;
let initialPageData: any;
router();

export default class Frame extends React.Component {
  state = {
    hoverPage: false,
    page: initialPage,
    pageData: initialPageData
  }
  render() {
    const contentOuterStyle = css`
      margin: 0 auto;
      max-width: 1300px;
      filter: ${this.state.hoverPage ? "blur(10px)" : "none"};
      transition: filter 300ms;
      pointer-events: ${this.state.hoverPage ? "none" : "auto"};
    `
    const contentInnerStyle = css`
      margin-top: 70px;
      width: 100%;
    `
    return (
      <div lang="zh-TW">
        <Global
          styles={css`
            body {
              overflow: ${this.state.hoverPage ? "hidden" : "auto"};
            }
          `}
        />
        <Header hideTopBar={this.state.hoverPage} page={this.state.page} toPage={this.toPage} />
        <div css={contentOuterStyle}>
          <div css={contentInnerStyle}>
            <Switch page={this.state.page} data={this.state.pageData} openArticle={this.openArticle} toPageNumber={this.toPageNumber} />
          </div>
          <Footer />
        </div>
        <HoverPage show={this.state.hoverPage} hideHoverPage={this.hideHoverPage} history={history}>
          {this.state.hoverPage ? (<ArticlePage data={this.state.pageData} />) : ""}
        </HoverPage>
      </div>
    )
  }
  componentDidMount = () => {
    window.addEventListener('popstate', this.urlChanged);
    OverlayScrollbars(document.body, { scrollbars: { autoHide: 'scroll' } });
  }
  urlChanged = () => {
    router();
    if (initialPage !== this.state.page || initialPageData !== this.state.pageData) {
      this.setState({ page: initialPage, pageData: initialPageData });
    }
  }
  showHoverPage = () => {
    this.setState({ hoverPage: true });
  }
  hideHoverPage = () => {
    this.setState({ hoverPage: false });
  }
  openArticle = (name: string) => {
    history.push(`/article/${name}/`);
    this.setState({ pageData: { articleName: name }, hoverPage: true })
  }
  toPage = (page: string, pageData: any) => {
    this.setState({ page, pageData });
    switch (page) {
      default:
      case "homepage":
        history.push('/');
        break;
    }
  }
  toPageNumber = (page: number) => {
    let data = this.state.pageData;
    data.pageNumber = page;
    this.setState({pageData: data});
    history.push(`/page/${page}`);
  }
}

type switchProps = {
  page: string;
  openArticle: Function;
  toPageNumber: Function;
  data: any;
}

class Switch extends React.Component<switchProps> {
  render() {
    let page;
    switch (this.props.page) {
      default:
      case "homepage":
        page = <Homepage openArticle={this.props.openArticle} toPageNumber={this.props.toPageNumber} data={this.props.data} />;
        break;
      case "article":
        page = (
          <Block>
            <ArticlePage data={this.props.data} />
          </Block>
        )
        break;
    }
    return page;
  }
}

function router() {
  // console.log(window.location.hostname, window.location.href);
  if (window.location.href.indexOf("/?jump=") !== -1) {
    // console.log("jump");
    let a = window.location.href.split("/?jump=");
    history.push(a[1] + "?");
  }
  if (window.location.pathname.startsWith("/article")) {
    initialPage = "article";
    let a = window.location.pathname.split("/");
    initialPageData = { articleName: a[2] }
  } else if (window.location.pathname.startsWith("/page")) {
    initialPage = "homepage";
    let a = window.location.pathname.split("/");
    initialPageData = { pageNumber: parseInt(a[2]) };
  } else {
    initialPage = "homepage";
    initialPageData = { pageNumber: 1 };
  }
}